<template>
<div class="home">
    <Card :key="question_detail.question_id" :title="question_detail.caption">
          <p>
              {{question_detail.content}}
          </p>
          <p></p>
          <p>提问时间：{{question_detail.create_time}} 提问者：{{question_detail.author_name}}</p>
      </Card>
  </div>
</template>

<script>
export default {
  name: "QuestionDetail",
  data() {
    return {
      question_detail:{},
      answer_list:[]
    };
  },
  created() {
    console.log(this.$route.query)
    this.fetchQuestionDetail(this.$route.query.question_id)
    this.fetchAnswerList(this.$route.query.question_id)
  },
  methods: {
    fetchQuestionDetail(question_id) {
        let vm = this
        this.$http.get("/api/question/detail?question_id="+question_id).then(function(response){
            console.log(response);
            if (response.status != 200) {
                vm.$Message.error("服务繁忙，请稍后重试");
                return;
            }
            if (response.data.code === 0) {
                console.log(response.data.data);
                vm.question_detail = response.data.data
            } else {
                vm.$Message.error(response.data.message);
            }
        }, function(response) {
            vm.$Message.error("服务繁忙，请稍后重试");
            console.log(response);
        }
      )
    },
    fetchAnswerList(question_id) {
        let vm = this
        this.$http.get("/api/answer/list?question_id="+question_id).then(function(response){
            console.log(response);
            if (response.status != 200) {
                vm.$Message.error("服务繁忙，请稍后重试");
                return;
            }
            if (response.data.code === 0) {
                console.log(response.data.data);
                vm.answer_list = response.data.data
            } else {
                vm.$Message.error(response.data.message);
            }
        }, function(response) {
            vm.$Message.error("服务繁忙，请稍后重试");
            console.log(response);
        }
      )
    }
  }
};
</script>

